.miniature h1,
.miniature h2,
.miniature h3,
.miniature h4 {
  margin-top: 0px;
}
.miniature {
  position: relative;
  display: flex;
  display: block;
  box-sizing: border-box;
  border-radius: 20px;
  height: 7.1rem;
  width: 7.1rem;
  margin: 0;
}
.shrink {
  top: -0.6rem;
  display: inline-block;
  /* box-sizing: content-box; */
  min-width: 3rem;
  width: 3rem;
  height: 3rem;
  margin: 0 0.16rem;
  border-radius: var(--border-radius);
}
.padding {
  padding: 0.8rem;
}
.lv {
  z-index: 2;
  display: block;
  height: min-content;
  text-align: right;
  max-height: 100%;
  max-width: 100%;
  font-size: 2.6rem;
  line-height: 2.3rem;
}
.shrink .lv {
  font-size: 1.4rem;
  line-height: 1.4rem;
}

.textBoxContainer {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  opacity: 0.5;
  width: 100%;
  height: 100%;
}
.bgColorFront {
  z-index: -1;
  position: absolute;
  top: -60%;
  left: -16%;
  width: 155%;
  height: 177%;
  font-size: 11rem;
  color: #051900;
  background-color: #72ffff;
  opacity: 0.2;
}
.shrink .bgColorFront {
  left: -20%;
  left: -13%;
  top: -70%;
  top: -46%;
  font-size: 4.4rem;
}

.textBoxBack {
  z-index: -2;
  position: absolute;
  left: 1.6rem;
  top: 1.8rem;
  font-weight: bold;
  font-size: 4rem;
  color: #051900;
  opacity: 0.3;
}

.imgFrame {
  height: 100%;
  width: 100%;

  box-sizing: border-box;
  padding: 0.1rem;

  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  background-color: #a5ffd7;
  position: relative;
  z-index: 2;
  border-radius: 20px;
  overflow: hidden;
  display: flex;
  object-fit: cover;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12),
    inset 0px -2px 4px -1px #ad8fb93e, inset 0px -4px 5px 0px #ad8fb926,
    inset 0px -1px 10px 0px #ad8fb918, 0px 0px 4px 1px #000;
}
.shrink .imgFrame {
  /* height: 3rem; */
  box-sizing: border-box;
  padding-top: 0.34rem;
  box-shadow: var(--box-shadow);
  border-radius: var(--border-radius);
}
